const isNil = val => {
  if (!Boolean(val)) {
    return "";
  }
};

const maxLengthCheck = (val, m, n) => {
  if (val.length > m) return ` ${m} `;
  if (val.length < n) return ` ${n} `;
};

const numberCheck = val => {
  if (isNaN(val)) return "";
};

// validator return data or true in case error occurs
const formObject = {
  firstName: {
    name: "firstName",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const tooLong = maxLengthCheck(val, 30);
      if (tooLong) return tooLong;
    }
  },
  lastName: {
    name: "lastName",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const tooLong = maxLengthCheck(val, 30);
      if (tooLong) return tooLong;
    }
  },
  sex: {
    name: "sex",
    validator: isNil,
    options: [
      { value: "MALE", text: "" },
      { value: "FEMALE", text: "" }
    ]
  },
  dob: { name: "dateOfBirth", validator: isNil },
  bank: {
    name: "bankName",
    options: [
      { value: "DBBK", text: " (DBBK)" },
      { value: "BBL", text: " (BBL)" },
      { value: "KBank", text: " (KBank)" },
      { value: "SCB", text: " (SCB)" },
      { value: "KTB", text: " (KTB)" },
      { value: "BAAC", text: " (BAAC)" },
      { value: "TMB", text: " (TMB)" },
      { value: "ICBC", text: " (ICBC)" },
      { value: "TCRB", text: " (TCRB)" },
      { value: "CITI", text: " (CITI)" },
      { value: "SMBC", text: "  (SMBC)" },
      { value: "SCBT", text: " (SCBT)" },
      { value: "CIMB", text: " (CIMB)" },
      { value: "UOB", text: " (UOB)" },
      { value: "BAY", text: " (BAY)" },
      { value: "GSB", text: " (GSB)" },
      { value: "HSBC", text: " (HSBC)" },
      { value: "MHCB", text: " (MHCB)" },
      { value: "GHB", text: ". (GHB)" },
      { value: "LHBANK", text: " (LHBANK)" },
      { value: "TBNK", text: " (TBNK)" },
      { value: "TISCO", text: " (TISCO)" },
      { value: "KK", text: " (KK)" },
      { value: "ISBT", text: " (ISBT)" }
    ],
    validator: isNil
  },
  accountNo: {
    name: "bankAccountNumber",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const notNumber = numberCheck(val);
      if (notNumber) return notNumber;
      const tooLong = maxLengthCheck(val, 16, 16);
      if (tooLong) return tooLong;
    }
  },
  email: {
    name: "email",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const incorrectFormat =
        !/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
          val
        ) && "";
      if (incorrectFormat) return incorrectFormat;
      const tooLong = maxLengthCheck(val, 50);
      if (tooLong) return tooLong;
    }
  },
  password: {
    name: "password",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const tooLong = maxLengthCheck(val, 20);
      if (tooLong) return tooLong;
      //   const incorrectFormat = /[0-9]/.test(val) || /[a]
    }
  },
  phoneNo: {
    name: "phoneNumber",
    validator: val => {
      const nil = isNil(val);
      if (nil) return nil;
      const notNumber = numberCheck(val);
      if (notNumber) return notNumber;
      const tooLong = maxLengthCheck(val, 12, 10);
      if (tooLong) return tooLong;
      //   const incorrectFormat = /[0-9]/.test(val) || /[a]
    }
  },
  lineId: {
    name: "lineId",
    validator: val => maxLengthCheck(val, 20)
  },
  howToFind: {
    name: "knowFrom",
    options: [
      { value: "facebook", text: "Facebook" },
      { value: "line", text: "Line" },
      { value: "google", text: "Google" },
      { value: "youtube", text: "Youtube" },
      { value: "instagram", text: "Instagram" },
      { value: "sms", text: "SMS" },
      { value: "friend", text: "" },
      { value: "etc", text: "" }
    ],
    validator: () => ""
  }
};

export default formObject;
